.Modal {
    position: absolute;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    -webkit-box-shadow: 1px 1px 1px #3b3a36;
            box-shadow: 1px 1px 1px #3b3a36;
    padding: 16px;
    left: 15%;
    top: 30%;

    -webkit-transition: all 0.3s ease-out;

    -o-transition: all 0.3s ease-out;

    transition: all 0.3s ease-out;

   
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}


